body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 1810px;
  font-family: 'TT Lakes'; }

@media (min-width: 768px) {
  body {
    min-width: 768px;
    min-height: 1857px;
    font-family: 'TT Lakes'; } }

@media (min-width: 1120px) {
  body {
    min-width: 1120px;
    min-height: 1925px;
    font-family: 'TT Lakes', sans-serif; }
  .main__conteiner {
    max-width: 1120px;
    min-height: 1925px;
    margin-left: auto;
    margin-right: auto; } }

@media (min-width: 1440px) {
  body {
    max-width: 1440px;
    margin: 0;
    min-height: 1925px;
    font-family: 'TT Lakes';
    margin-right: auto;
    margin-left: auto; }
  .container {
    max-width: 1440px;
    min-height: 1925px;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: 320px 1120px;
    position: relative; }
  .main__conteiner {
    width: 1120px;
    min-height: 1925px; } }
